@media (max-width: 767px) {
  .main-nav__list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--opened .main-nav__list {
  display: block;
}
.main-nav__toggle {
  background-color: transparent;
  border: none;
  position: absolute;
  top: -39px;
  right: 20px;
  width: 23px;
  height: 22px;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 23px;
  height: 4px;
  left: 0;
  top: 0;
  background-color: #81b3d2;
  box-shadow: 0 9px #81b3d2, 0 18px #81b3d2;
}
.main-nav--opened .main-nav__toggle::before {
  content: "";
  position: absolute;
  width: 28px;
  height: 4px;
  left: 0;
  top: 50%;
  background-color: #ffffff;
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  width: 28px;
  height: 4px;
  left: 0;
  top: 50%;
  background-color: #ffffff;
  transform: rotate(-45deg);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: block;
  position: static;
  margin-top: 50px;
}
.main-nav--nojs .main-nav__wrapper {
  display: block;
  position: static;
  margin-top: 50px;
}
.page__body {
  background-color: #f2f2f2;
}
@media (max-width: 767px) {
  .site-list {
    background-color: #81b3d2;
  }
}
@media (min-width: 768px) {
  .site-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    row-gap: 42px;
    background-color: #ffffff;
  }
}
.site-list__link {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #ffffff;
  background-color: #81b3d2;
  text-decoration: none;
  display: block;
  padding: 17px 50px 12px;
  text-align: center;
}
@media (max-width: 767px) {
  .site-list__link {
    box-shadow: inset 0 1px rgba(255, 255, 255, 0.2), inset 0 -1px rgba(1, 1, 1, 0.2);
  }
}
@media (min-width: 768px) {
  .site-list__link {
    font-size: 14px;
    line-height: 21px;
    background-color: transparent;
    color: #000000;
    padding: 18px 10px;
  }
}
@media (min-width: 1200px) {
  .site-list__link {
    margin: 0 50px;
  }
}
@media (max-width: 767px) {
  .site-list__item:first-child .site-list__link {
    box-shadow: inset 0 -1px rgba(1, 1, 1, 0.2);
  }
}
@media (max-width: 767px) {
  .site-list__item:last-child .site-list__link {
    box-shadow: inset 0 1px rgba(255, 255, 255, 0.2);
    position: relative;
  }
}
@media (max-width: 767px) {
  .site-list__item:last-child .site-list__link::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 7px;
    left: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
}
.site-list__item--logo {
  display: none;
}
@media (min-width: 768px) {
  .site-list__item--logo {
    display: block;
    grid-column: 3 / 4;
  }
}
@media (min-width: 768px) {
  .site-list__link--active {
    color: #766357;
  }
}
.social {
  display: flex;
  gap: 6px;
}
.social__link {
  background-color: #81b3d2;
  width: 48px;
  height: 48px;
}
.special-link {
  font-size: 14px;
  color: #000000;
  text-decoration: none;
}
.checkbox-select__ist {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .checkbox-select__ist {
    margin: 0 84px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 32px;
    justify-items: center;
  }
}
@media (min-width: 1200px) {
  .checkbox-select__ist {
    margin: 0 53px;
    grid-template-columns: repeat(4, 1fr);
    gap: 28px;
  }
}
.checkbox-select__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: initial;
}
.radio-select__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .radio-select__list {
    margin: 0 36px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
    justify-items: center;
  }
}
@media (min-width: 1200px) {
  .radio-select__list {
    margin: 0 53px;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 28px;
  }
}
.radio-select__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: initial;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
html {
  height: 100%;
}
img {
  max-width: 100%;
  object-fit: contain;
}
body {
  margin: 0;
  min-height: 100%;
  min-width: 320px;
}
.site-wrapper {
  background-color: #ffffff;
}
